import React, { useCallback, useMemo } from "react"
import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { Box, Card } from "@rp/react/web/mui/core"
import { useNavigateToBriefConfigDetailView } from "../../hooks"
import { BriefConfigCardActionsDango } from "./BriefConfigCardActionsDango"
import { BriefConfigCardName } from "./BriefConfigCardName"
import { BriefConfigCardOverview } from "./BriefConfigCardOverview"
import { BriefConfigCardUser } from "./BriefConfigCardUser"
import { BriefConfigScheduleInfo } from "./BriefConfigScheduleInfo"
import { getContainerSX } from "./briefConfigCard.utils"

type BriefConfigCardProps = { userBriefConfig: BriefConfig; idx: number; isHomePage?: boolean }

const BriefConfigCard = ({ userBriefConfig, idx, isHomePage = false }: BriefConfigCardProps) => {
  const { briefName, id, schedule } = userBriefConfig
  const { navigateToBriefConfigDetailView } = useNavigateToBriefConfigDetailView()
  const handleClick = useCallback(
    () => navigateToBriefConfigDetailView(id),
    [id, navigateToBriefConfigDetailView],
  )

  const containerSX = useMemo(() => getContainerSX(isHomePage, idx), [idx, isHomePage])

  return (
    <Card role="article" aria-label="brief card" sx={containerSX} onClick={handleClick}>
      <Box sx={{ display: "flex", justifyContent: "space-between", minHeight: "32px" }}>
        <BriefConfigScheduleInfo schedule={schedule} />
        {!isHomePage && <BriefConfigCardActionsDango briefConfig={userBriefConfig} />}
      </Box>

      <BriefConfigCardName name={briefName} />

      <BriefConfigCardOverview overview={userBriefConfig.overview} />

      <BriefConfigCardUser />
    </Card>
  )
}

export { BriefConfigCard }
